import React, { FC, useState } from 'react';
import styles from './stake_table.module.scss';
import cn from 'classnames';
import Icon from '../../../assets/icons/black_icon.svg';
import RightArrow from '../../../assets/icons/right_arrow_icon.svg';

const fakeData = [
  { name: 'Pool Name', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
  { name: 'Pool Name', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
  { name: 'Pool Name', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
  { name: 'Pool Name', percent: '0,001', value: '0,175 $', date: new Date(Date.now()) },
];
interface Props {}

export const StakeTable: FC<Props> = () => {
  const [isDataShown, setDatasState] = useState(false);

  const timeConverter = (data: Date) => {
    let date = new Date(data);
    let day = date.getDay();
    let month = date.getMonth() + 1;
    let year = date.getFullYear();
    let hours = date.getHours();
    let minutes = date.getMinutes();

    let fixedDay = day < 10 ? '0' + day : day;
    let fixedMonth = month < 10 ? '0' + month : month;
    let fixedHours = hours < 10 ? '0' + hours : hours;
    let fixedMinutes = minutes < 10 ? '0' + minutes : minutes;

    return fixedDay + '.' + fixedMonth + '.' + year + ' ' + fixedHours + ':' + fixedMinutes + ' UTC';
  };

  const dataToggle = () => {
    setDatasState(!isDataShown);
  };

  return (
    <section className={styles.wrapper}>
      <div className={styles.dataBlock}>
        <div className={styles.dataWrapper}>
          <p className={styles.title}>Mein Stake</p>
        </div>
        <div className={styles.dataWrapper}>
          <p className={styles.title}>Anteil:</p>
          <p className={styles.title}>10,223 MNDCC</p>
          <p className={styles.percent}>{'(10,07%)'}</p>
        </div>
        <div className={styles.dataWrapper}>
          <p className={styles.title}>Reward Konto:</p>
          <p className={styles.greenTitle}>1,203 MNDCC</p>
        </div>
        <div className={styles.dataWrapper}>
          <p className={styles.title}>APY:</p>
          <p className={styles.greenTitle}>25,89%</p>
        </div>
      </div>
      <div className={styles.tableTitles}>
        <div className={styles.tableTitle} style={{ width: '31rem' }}>
          <p style={{ marginLeft: '2rem', marginBottom: 0 }}>Pool</p>
        </div>
        <div className={styles.tableTitle} style={{ width: '15.5rem' }}>
          Anteile
        </div>
        <div className={styles.tableTitle} style={{ width: '32rem' }}>
          MNDCC
        </div>
        <div className={styles.tableTitle} style={{ width: '22rem' }}>
          Wert <span style={{ color: '#9A9A9A' }}>USD</span>
        </div>
        <div className={styles.tableTitle} style={{ width: '21rem' }}>
          Anzahl Rewards
        </div>
        <div className={styles.tableTitle} style={{ width: '10rem' }}>
          Aktionen
        </div>
      </div>
      <div className={styles.dataContainer}>
        <div className={styles.tableTitle} style={{ width: '36rem' }}>
          Pool Name
        </div>
        <div className={styles.tableTitle} style={{ width: '21rem' }}>
          10,07%
        </div>
        <div className={styles.tableTitle} style={{ width: '36rem' }}>
          10,223
        </div>
        <div className={styles.tableTitle} style={{ width: '28rem' }}>
          180,88 $
        </div>
        <div className={styles.tableTitle} style={{ width: '22rem' }}>
          {'37'}
          <img
            src={RightArrow}
            alt="arrow"
            className={cn(styles.arrow, { [styles.arrowRotated]: isDataShown })}
            onClick={dataToggle}
          />
        </div>
        <div className={styles.tableTitle} style={{ width: '15rem' }}>
          <img src={Icon} alt="icon" className={styles.icon} />
          <img src={Icon} alt="icon" className={styles.icon} />
          <img src={Icon} alt="icon" className={styles.icon} />
        </div>
      </div>
      <div className={cn(styles.dataHidden, { [styles.dataShown]: isDataShown })}>
        {fakeData.map((data, i) => (
          <div className={cn(styles.mappedDataContainer)} key={i}>
            <p className={styles.dataItem} style={{ width: '57rem' }}>
              {data.name}
            </p>
            <p className={styles.dataItem} style={{ width: '23rem' }}>
              +{data.percent}
            </p>
            <p className={styles.dataItem} style={{ width: '27rem' }}>
              +{data.value}
            </p>
            <p className={styles.dataItem}>{'Auszahlung: ' + timeConverter(data.date)}</p>
          </div>
        ))}
      </div>
    </section>
  );
};
